@import url(https://fonts.googleapis.com/css?family=Muli:500,600,700,800,900&display=swap);
body{font-family:'Muli', sans-serif}body{margin:0;background-color:#382F30}body .App{height:100vh}.joke-card{position:relative;display:flex;flex-direction:column;justify-content:space-between;min-height:120px;padding:48px;padding-right:56px;border-radius:10px;background-color:#fff;color:#000;font-size:32px;font-weight:600;transition:.3s ease-in-out;max-height:500px}.joke-card .joke-type-icon{position:absolute;top:8px;right:10px;width:32px;height:32px}.joke-wrapper .joke-card{margin-top:100px;max-height:500px}.joke-wrapper .custom-button{margin:16px auto 0 auto}.wrapper{max-width:1000px;margin:0 auto}.custom-button{-webkit-appearance:none;-moz-appearance:none;appearance:none;min-width:80px;padding:8px 16px;background-color:#C14948;color:#fff;border:none;border-radius:5px;cursor:pointer;font-family:inherit;font-size:20px;transition:.2s ease-in-out}.custom-button:hover{background-color:#a03736}.custom-button:focus{outline:none;box-shadow:0 0 0 2px #ce6f6e}.custom-button[disabled]{color:rgba(255,255,255,0.3);background-color:rgba(193,73,72,0.3);cursor:not-allowed}.custom-header{padding:16px 0}.custom-header .wrapper{display:flex;align-items:center}.custom-header .wrapper .header-icon{width:48px;height:48px}.custom-header .wrapper span{color:#fff;font-weight:900;font-size:48px;margin-left:16px}.custom-icon{background:no-repeat center;background-size:cover}.loading-skeleton div{display:inline-block;width:100%;height:32px;background-color:#ccc;border-radius:10px;background-image:linear-gradient(90deg, #ddd 0px, #e8e8e8 40px, #ddd 140px);background-size:1000px;-webkit-animation:loading-gradient 1.6s infinite linear;animation:loading-gradient 1.6s infinite linear}.loading-skeleton div:first-child{width:70%}.loading-skeleton div:last-child{width:50%}.loading-skeleton div:not(:last-child){margin-bottom:16px}@-webkit-keyframes loading-gradient{40%{background-position:600px}100%{background-position:1000px}}@keyframes loading-gradient{40%{background-position:600px}100%{background-position:1000px}}

